:root {
  --c--background: black;
  --c--color: green;
  --c--color-light: greenyellow;
  --c--button: #0c1806;
  --b-border: solid green 1px;
  --b-border-light: solid greenyellow 1px;
}

* {
  margin: 0;
  padding: 0;
  font-size: 62.5%;
}

body {
  background-color: var(--c--background);
  height: 100vh;
  display: flex;
  justify-content: center;
  font-size: 10rem;
  color: var(--c--color);
}
